import React from 'react';


const Tutorial = () => {
    

  return (
    <aside className=' pr2'>
        <div className='h4'></div>
        <h3>Uzdevumu pildana</h3>
        <ol className=''>
            <li className='mt2'>No skuma kreisaj pus izvlies tmu. Spied uz kdas klases un tad uz kdas no tmm.</li>
            <li className='mt2'>Tad pa labi no tmm izvlies kdu uzdevumu un spied uz t.</li>
            <li className='mt2'>Tagad ekrna vid ir pardjies uzdevums, kura atbildi jieraksta atbildes lauk. Ja atbildi nezini, tad spied "Pardt atbildi".</li>
        </ol>
   </aside>
  )

}


export default Tutorial